"use client";

import { useEffect, useState } from "react";

export function Navbar() {
    const [scrolled, setScrolled] = useState(false);
    const [menuOpen, setMenuOpen] = useState(false);

    useEffect(() => {
        const onScroll = () => setScrolled(window.scrollY > 10);
        window.addEventListener("scroll", onScroll);
        return () => window.removeEventListener("scroll", onScroll);
    }, []);

    return (
        <nav
            className={`fixed inset-x-0 top-0 z-50 border-b transition-all duration-300 ${scrolled ? "border-slate-200/80 bg-white/95 backdrop-blur-xl" : "border-transparent bg-transparent"
                }`}
        >
            <div className="mx-auto flex h-16 w-full max-w-6xl items-center justify-between px-4 sm:h-[68px] sm:px-6 lg:px-10">
                <a href="#" className="flex items-center gap-2.5 no-underline">
                    <div className="flex h-9 w-9 items-center justify-center rounded-xl bg-gradient-to-br from-indigo-500 to-sky-500 shadow-[0_4px_12px_rgba(99,102,241,0.35)]">
                        <svg width="19" height="19" viewBox="0 0 24 24" fill="none" stroke="white" strokeWidth="2.2" strokeLinecap="round" strokeLinejoin="round">
                            <path d="M13 2L3 14h9l-1 8 10-12h-9l1-8z" />
                        </svg>
                    </div>
                    <span className="text-xl font-extrabold tracking-[-0.04em] text-slate-900">
                        Form<span className="bg-gradient-to-br from-indigo-500 to-sky-500 bg-clip-text text-transparent">AI</span>
                    </span>
                </a>

                <div className="hidden items-center gap-1 sm:flex">
                    {["Features", "How it works", "Pricing"].map((item) => (
                        <a
                            key={item}
                            href="#"
                            className="rounded-lg px-3 py-1.5 text-sm font-medium text-slate-600 transition hover:bg-slate-100 hover:text-slate-900"
                        >
                            {item}
                        </a>
                    ))}
                </div>

                <div className="flex items-center gap-2 sm:gap-2.5">
                    <button
                        className="inline-flex h-9 w-9 items-center justify-center rounded-lg border border-slate-200 text-slate-700 sm:hidden"
                        onClick={() => setMenuOpen((prev) => !prev)}
                        aria-label="Toggle menu"
                    >
                        <span className="text-lg leading-none">{menuOpen ? "√ó" : "‚â°"}</span>
                    </button>
                    <button className="hidden rounded-lg px-3 py-1.5 text-sm font-semibold text-slate-600 transition hover:bg-slate-100 hover:text-slate-900 sm:inline-flex">
                        Log in
                    </button>
                    <button className="rounded-xl bg-gradient-to-br from-indigo-500 to-sky-500 px-4 py-2 text-sm font-bold text-white shadow-[0_4px_14px_rgba(99,102,241,0.4)] transition hover:-translate-y-0.5 hover:shadow-[0_6px_20px_rgba(99,102,241,0.5)] sm:px-5">
                        Get Started Free
                    </button>
                </div>
            </div>

            {menuOpen ? (
                <div className="border-t border-slate-200 bg-white px-4 py-3 sm:hidden">
                    <div className="flex flex-col gap-1">
                        {["Features", "How it works", "Pricing"].map((item) => (
                            <a
                                key={item}
                                href="#"
                                onClick={() => setMenuOpen(false)}
                                className="rounded-lg px-3 py-2 text-sm font-medium text-slate-700 transition hover:bg-slate-100"
                            >
                                {item}
                            </a>
                        ))}
                        <button className="mt-1 rounded-lg px-3 py-2 text-left text-sm font-semibold text-slate-700 transition hover:bg-slate-100">
                            Log in
                        </button>
                    </div>
                </div>
            ) : null}
        </nav>
    );
}
